@media screen and (max-width: 991px){
    .heading_grid {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        padding-top: 20px;
    }}

@media screen and (max-width: 767px){
    .heading_grid {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        align-items: stretch;
        grid-auto-flow: row;
        grid-column-gap: 15px;
        grid-row-gap: 15px;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
    }
    .w-col {
        width: 100%;
        left: auto;
        right: auto;
    }
}

@media screen and (max-width: 479px){
    .heading_grid {

        padding-top: 0;
    }
    .heading_grid {
        display: -ms-grid;
        display: grid;
        margin-left: auto;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        justify-items: stretch;
        -webkit-box-align: end;
        -webkit-align-items: end;
        -ms-flex-align: end;
        align-items: end;
        grid-auto-columns: 1fr;
        grid-column-gap: 15px;
        grid-row-gap: 0px;
        -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
        -ms-grid-rows: auto;
        grid-template-rows: auto;
    }
    .column-37 {
        padding-right: 0;
        padding-left: 0;
    }
    .column-17 {
        height: 100%;
        padding-left: 0;
    }
    .w-col-2 {
        width: 0;
    }
}