.like_section{
    display: block;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.section_container {
    display: block;
    margin-right: 0;
    margin-left: 0;
    padding: 0 0 5%;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    -ms-grid-row-align: auto;
    align-self: auto;
}
.left{
    grid-area: left;
}

.right{
    grid-area: right;
    text-align: left;
}

.like_grid{
    display: grid;
    min-height: 0;
    align-content: stretch;
    grid-template-columns: 0.5fr 3fr;
    grid-column-gap: 30px;
    grid-row-gap: 16px;
    grid-template-rows: auto;
    grid-template-areas:
            "left right"
            ". right"
}

@media screen and (max-width: 991px){
    .like_grid{
        display: grid;
        min-height: 0;
        align-content: stretch;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        grid-template-areas:
            "left ."
            "right right"
    }
}