/*STYLES*/

.content {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: #333;
}

.in_container {
    min-height: 0;
    margin-top: 5%;
}

.section_container {
    display: block;
    margin-right: 0;
    margin-left: 0;
    padding: 0 0 5%;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-self: auto;
    -ms-flex-item-align: auto;
    -ms-grid-row-align: auto;
    align-self: auto;
}

.heading_container.title_hero{
    margin-top: 11%;
}

.heading_container {
    /*position: relative;*/
    display: block;
    /*max-width: 1280px;*/
    /*margin-right: auto;*/
    /*margin-left: auto;*/
    /*padding: 7% 0 5%;*/
}

.main_heading_1 {
    position: static;
    display: inline-block;
    width: auto;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translate(0,0);
    -ms-transform: translate(0,0);
    transform: translate(0,0);
    -webkit-transform-origin: 50% 50%;
    -ms-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    font-family: 'IBM Plex Sans',sans-serif;
    color: #000;
    font-size: 84px;
    line-height: 84px;
    font-weight: 500;
    text-align: left;
}

.heading_2 {
    display: block;
    overflow: visible;
    margin-top: 5px;
    margin-right: 0;
    margin-bottom: 5px;
    padding-right: 0;
    float: none;
    border: 1px #000;
    font-family: 'IBM Plex Sans',sans-serif;
    color: #de3831;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    text-align: right;
    text-transform: uppercase;
}

.heading_2_wrapper {
    min-height: 40px;
    padding-right: 15px;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    border-right: 2px solid #de3831;
}

.grad_wrapper {
    height: auto;
}

.grad_text {
    font-size: 72px;
    background: linear-gradient(130deg, #d92f27 0%, #085399 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.grad_text.main_heading_1{
    font-size: 84px;
    line-height: 100px;
}


.main_button {
    transition: .5s;
    filter: contrast(100%);
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 254px;
    height: 56px;
    margin-top: 27px;
    padding: 15px 25px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    border: 0 #de3831;
    border-radius: 28px;
    background-color: transparent;
    background-image: linear-gradient(
            124deg
            ,#de3831,#00549f);
    box-shadow: 0 16px 37px -16px rgb(104 17 45 / 50%);
    font-family: 'IBM Plex Sans',sans-serif;
    color: #fff;
    font-size: 20px;
    line-height: 26px;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
}

.feedback_content {
    height: auto;
}

.body_container{
    position: relative;
    display: block;
    max-width: 1280px;
    margin-right: 1%;
    margin-left: 1%;
    padding: 7% 0 5%;
}

/*Slides*/
.slide5 {
    display: inline-block;
    vertical-align: top;
    width: 90vw;
    position: relative;
    height: 100%;

    white-space: normal;
    text-align: left;
}

@media (max-width: 991px) {
    .slide5 {width: 100vw}
    .heading_2_wrapper {
        border-right: none;
    }
    .body_container{
        position: relative;
        display: block;
        max-width: 1280px;
        margin-right: 0;
        margin-left: 0;
        padding: 7% 0 5%;
    }
    /*.heading_container {*/
    /*    max-width: 100%;*/
    /*    margin-right: auto;*/
    /*    margin-left: auto;*/
    /*    padding: 7% 0 5%;*/
    /*}*/

    .main_heading_1 {
        display: inline;
        font-size: 59px;
        line-height: 70px;
    }

    .heading_2 {
        text-align: left;
        font-size: 12px;
        line-height: 18px;
    }

    .grad_wrapper {
        max-height: 110px;
    }
    .grad_text{
        font-size: 70px;
        line-height: 70px;
    }
    .grad_text.main_heading_1{
        font-size: 60px;
        line-height: 75px;
    }
}

@media screen and (max-width: 767px) {

    .grad_wrapper {
        height: 65px;
    }

    .main_heading_1 {
        font-size: 48px;
        line-height: 56px;
    }
    .grad_text{
        display: flex;
        justify-content: flex-start;
        font-size: 53px;
        line-height: 61px;
    }
    .main_button{
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
        -ms-flex-direction: row;
        flex-direction: row;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
    }
    .grad_text.main_heading_1{
        font-size: 48px;
        line-height: 70px;
    }
}
@media screen and (max-width: 479px) {
    .heading_container{
        margin-top: 10%;
        margin-bottom: 10%;
        align-self: flex-start;
    }

    .section_container{
        margin-bottom: 10px;
    }

    .main_heading_1 {
        font-size: 34px;
        line-height: 40px;
    }

    .main_button {
        width: 100%;
    }
    .grad_wrapper{
        height: 55px;
    }
    .grad_text{
        display: block;
        margin-top: 10px;
        font-size: 36px;
        line-height: 40px;
    }
    .grad_text.main_heading_1{
        margin-top: 0;
        font-size: 34px;
        line-height: 50px;
    }
    .heading_container.title_hero {
        margin-top: 20%;
    }

}