.footer {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    padding-top: 3%;
    padding-bottom: 3%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #fafafa;
}

.section_container.footer_container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    padding-top: 3%;
    padding-bottom: 3%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}

.footer_logo{
    display: block;
    margin: 0 auto;
    opacity: 1;
    filter: contrast(100%);
    transition: 3s;
}

.footer_navbar{
    display: flex;
    width: 100%;
    padding-top: 30px;
    padding-bottom: 20px;
    flex-direction: row;
    justify-content: center;
    flex: 0 auto;
    /*border-bottom: 1px #e9e9e9 solid*/
}

.links_wrapper{
    display: grid;
    margin-right: auto;
    margin-left: auto;
    justify-content: center;
    justify-items: stretch;
    align-items: stretch;
    grid-auto-columns: 1fr;
    grid-column-gap: 70px;
    grid-row-gap: 15px;
    grid-template-rows: 30px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'IBM Plex Sans', sans-serif;

}
.links_wrapper a:hover{
    text-decoration: none;
    transition: 0.5s;
    color: rgba(195,59,61,255)
}

.foot_bar {
    display: flex;
    justify-content: center;
    max-width:100%

}
.star {
    width: 16px;
    filter: opacity(0.2) drop-shadow(0 0 0 #ff4830) drop-shadow(0 0 0 #ff4830) drop-shadow(0 0 0 #ff4830);
    height: 100%;
    content: "";
    display: block;
    min-height: 16px;
    margin: 1px;
}
.footer_links{
    display: flex;
    padding: 10px 0 20px 0;
    justify-content:center;
}
.footer_links img{
    padding: 0 5px
}

.logotype {
    width: 82px;
    height: 23px;
    display: block;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
}
.reviews {
    grid-area: review;
    text-align: left;
    color: #6a797e;
    text-decoration: none;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: inline-block;
    font-family: Roboto,sans-serif;
    margin-top: 7px;
    white-space: nowrap;
}
.logoText {
    grid-area: text;
    text-align: right;
    font-size: 11px;
    color: #6a797e;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: block;
    margin: 0 0 5px;
    white-space: nowrap;
    font-family: Roboto,sans-serif;

}
.clutch {
    grid-area: clutch;
    width: 82px;
    height: 23px;
    display: block;
    justify-self: right;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    background-image: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 87.861 25"><path fill="%2317313b" d="M22.861 0h4v25h-4zm18 17.025c0 3.826-3.217 4.131-4.174 4.131-2.391 0-2.826-2.238-2.826-3.588V8h-4v9.548c0 2.37.744 4.326 2.048 5.63 1.152 1.153 2.878 1.783 4.748 1.783 1.326 0 3.204-.413 4.204-1.326V25h4V8h-4v9.025zM52.861 2h-4v6h-3v4h3v13h4V12h3V8h-3zm15.597 17.917c-.871.783-2.021 1.217-3.283 1.217-2.782 0-4.825-2.043-4.825-4.848s1.978-4.762 4.825-4.762c1.24 0 2.412.413 3.305 1.196l.607.522 2.697-2.696-.675-.609C69.522 8.504 67.415 7.7 65.174 7.7c-5 0-8.631 3.608-8.631 8.565 0 4.936 3.718 8.673 8.631 8.673 2.283 0 4.412-.804 5.979-2.26l.652-.609-2.739-2.694-.608.542zM86.061 9.482C84.909 8.33 83.559 7.7 81.689 7.7c-1.326 0-2.828.413-3.828 1.325V0h-4v25h4v-9.365c0-3.826 2.718-4.13 3.675-4.13 2.391 0 2.325 2.239 2.325 3.587V25h4v-9.887c0-2.37-.495-4.326-1.8-5.631"/><path fill="%23FF3D2E" d="M65.043 13.438a2.891 2.891 0 110 5.784 2.891 2.891 0 010-5.784"/><path fill="%2317313b" d="M17.261 18.721c-1.521 1.565-3.587 2.413-5.761 2.413-4.456 0-7.696-3.5-7.696-8.304 0-4.826 3.24-8.326 7.696-8.326 2.153 0 4.196.847 5.74 2.391l.608.609 2.674-2.674-.587-.609C17.718 1.938 14.718.7 11.5.7 4.935.7 0 5.917 0 12.851 0 19.764 4.957 24.96 11.5 24.96c3.24 0 6.24-1.26 8.457-3.543l.587-.609-2.652-2.717-.631.63z"/></svg>');
}
.stars {
    grid-area: stars;
    text-align: left;
    display: flex;
}

.widgets {
    display: grid;
    /*padding: 40px 0 10px 0;*/
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-column-gap: 16px;
    grid-template-areas:
            "text  stars"
            "clutch  review"
}

.footer_logo:hover{
    filter: contrast(200%);
}

.footer_links img{

    width: 45px;
    height:auto;
    vertical-align: middle;
    display: inline-block;
}
.footer_links img:hover{
    filter: opacity(0.3);
    cursor: pointer;
    border: 0;
    transition: 2s

}
.copyright{
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    padding: 10px 20px;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    font-family: 'IBM Plex Sans',sans-serif;
    font-size: 13px;
    font-weight: 400;
}

@media screen and (max-width: 991px){
    .footer_logo{
        max-width:88px;
    }
    .footer_navbar{
        border: none;
        text-align: center;
    }
}

@media screen and (max-width: 767px){
    .links_wrapper{
        grid-column-gap: 21px;
    }
}

@media (max-width: 476px) {
    .links_wrapper {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: column;
        flex: 1;
    }
    .links_wrapper a{
            padding: 10px 0;
    }
    .footer_logo{
        align-self: center;
        width: 66px;
    }
    .copyright{
        text-align: center;
    }

}








