body{
    display: block;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    background-image: none;
    background-repeat: repeat;
    background-attachment: scroll;
    font-family: 'IBM Plex Sans',sans-serif;
    margin: 0;
    min-height: 100%;
    background-color: #fff;
    font-size: 14px;
    line-height: 20px;
    color: #333;
}

.img{
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
}

.navbar {
    z-index: 10;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: auto;
    margin-left: 0;
    padding-top: 0;
    padding-right: 0;
    padding-left: 0;
    clear: none;
    flex: 1;
    background-color: #fff;

}
.w_container{
    margin-left: auto;
    margin-right: auto;
    max-width: 940px;
}
.up_nav{
    display: contents;
}

.header{
    box-sizing: content-box;
    z-index: 2;
    display: block;
    max-width: 1246px;
    min-width: 80%;
    margin-right: auto;
    margin-bottom: 0;
    margin-left: auto;
    padding-top: 0;
    padding-right: 5%;
    padding-left: 5%;
    clear: none;
    justify-content: flex-start;
    flex-wrap: nowrap;
    grid-auto-columns: 1fr;
    grid-column-gap: 16px;
    grid-row-gap: 16px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    background-color: #fff;
    text-align: left;
}



.logo{
    display: inline-block;
    color: transparent;
    text-align: left;
}

.logo_img{
    filter: contrast(100%);
}

.logo_img:hover{
    filter: contrast(200%);
    transition: 2s;
}
.menu_toggle{
    position: relative;
    float: right;
    padding: 18px;
    font-size: 18px;
    display: none;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    tap-highlight-color: rgba(0,0,0,0);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}



.header div a.logo:hover,
.header div a.logo{
    text-decoration: none;
    color: black;
}

.shake{
    animation: shake ;
    transition: all 2s ease-in-out;
    -o-transition       : all 2s ease-in-out;
    -moz-transition     : all 3s ease-in-out;
    -webkit-transition  : all 3s ease-in-out;
}

@keyframes shake {
    25%  { transform:  scale(1)}
    50% { transform:  scale(0.5)}
    75% { transform:  scale(1.3)}
    100%{ transform:  scale(1)}
}

nav{
    margin-left: 0;
    padding-left: 0;
    clear: none;
    position: relative;
    float: right;

}
.clearfix {
    clear: both;
}

.link{
    position: relative;
    display: inline-block;
    background: white;
    vertical-align: top;
    text-decoration: none;
    color: #222;
    padding: 23.2px;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
    transition: all .3s;
    cursor: pointer;
    border-radius: 5px;
    list-style: none;
    font-size: 15px;
}

.company {
    margin-left: 0;
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
    max-width: 1280px;
    color: rgb(0, 0, 0);
}

.contact_button{
    margin-top: 9px;
    padding: 11px 17px;
    border-radius: 22px;
    background-color: transparent;
    font-family: 'IBM Plex Sans',sans-serif;
    color: #333;
    text-decoration: none;
    text-transform: uppercase;
    border-width: 1px;
    border-color: #333;
}

.work .careers{
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
    max-width: 1280px;
    color: rgb(0, 0, 0);
}

.current {
    color: #de3831;
}


.link:hover {
    text-decoration: none;
    transition: 0.5s;
    color: rgba(195,59,61,255)
}

.contact{
    border: 1px black solid;
    background-color: transparent;
    color: rgb(0, 0, 0);
    margin-top: 8px;
    padding: 9px 15px;
    border-radius: 20px;
    font-family: 'IBM Plex Sans',sans-serif;
    text-decoration: none;
    text-transform: uppercase;
}


@media (max-width:991px){
    .logo_img{
        max-width: 88px;
        margin-top: 5px;
    }

    nav{
        z-index: -10;
        transform: translateY(-600px) translateX(0);
        transition: transform 700ms ease 0s;
        display: flex;
        flex-flow: column wrap;
        text-align: left;
        background-color: #fff;
        box-shadow: 0 5px 5px 0 rgb(50 50 50 / 50%);
        color: #000;
        float: none;
    }
    .contact_button{
        margin-top: 0;
        padding: 20px;
        border-style: none;
        border-radius: 0;
        font-weight: 600;
    }
    nav.active{
        z-index: -10;
        transform: translateY(0px) translateX(0px);
    }
    .link{
        width: 100%;
        text-align: left;
        margin-left: 0;
        margin-right: 0;
    }

    .contact_button{
        border: none;
    }

    @keyframes shake {
        0%  { transform: translateX(0)}
        30% { transform:  translateX(+100%)}
        60% { transform:  translateX(-30%)}
        70% { transform:  translateX(+10%)}
        90% { transform:  translateX(-1%)}
        100%{ transform:  translateX(0)}
    }
    .up_nav{
        display: block;
        padding: 5px 4%
    }
    .header{
        height: 60px;
        padding: 0;
    }
    .menu_toggle{
        display: block;
    }
