    /*    background-color: white;*/
    /*    width: 100vw;*/
    /*    display: block;*/
    /*    height: 250px;*/
    /*    background-color: white;*/
    /*    box-shadow: 0 15px 10px -15px #111;*/

    /*}*/

    .contact{
        border: none;
        background-color: white;

    }

}

@media screen and (max-width: 767px) {
    nav.active{
        transform: translateY(-10px) translateX(0px);
    }
}

@media screen and (max-width: 479px){
    .logo_img{
        max-width: 66px;
    }
}